.infoDiv{
  font-family: Arial, Helvetica, sans-serif;
  font-size: 16px;
  background: #fff;
  color: #404040;
  display: -ms-flexbox;
  display: flex;
  max-width: 500px;
  margin: 5% auto;
  -ms-flex-direction: column;
      flex-direction: column;
  max-height: 800px;
}
.usersList {
  margin-top: 5em;
  margin-left: 8%;
}

.miniInfo{
  font-family: Arial, Helvetica, sans-serif;
  font-size: 16px;
  background: #fff;
  color: #404040;
  display: -ms-flexbox;
  display: flex;
  max-width: 500px;
  margin-top: 2em;
  -ms-flex-direction: row;
      flex-direction: row;
  max-height: 200px;
