}

.imgDiv {
  -ms-flex: 1 1;
      flex: 1 1;
  height: 150px;
  width: 150px;
  color: #666666;
  padding: 1em 1em 1em;
}

.infoDiv a{
  color: #666666;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 16px;
}

.moreInfo{
  margin-top: 1.5em;
  max-width: 500px;
  word-wrap: break-word;
}

@media (max-width: 700px) {
  .infoDiv{
    margin-bottom: 50%;
  }
}

@media (max-width: 600px) {

  .infoDiv{
    margin-left: 10px;
    margin-right: 10px;
  }

}

@media (max-width: 400px) {



    .mainInfo{
      margin-left: 0;
    }

    .mainDiv {
      -ms-flex-direction: column;
          flex-direction: column;
    }
}



